.btn {
    width: 90px;
    height: 30px;
    font-size: 14px;
    font-weight: 600;
    border: none;
    outline: none;
    border-radius: 4px;
  }
  
  .btn:hover {
    transform: scale(1.1);
  }
  
  .btn:not(:last-child) {
    margin-right: 15px;
  }
  
  .good {
    composes: btn;
    background-color: rgba(17, 114, 17, 0.678);
  }
  
  .neutral {
    composes: btn;
    background-color: rgba(192, 192, 192, 0.671);
  }
  
  .bad {
    composes: btn;
    background-color: rgba(241, 28, 28, 0.658);
  }